import React from 'react';
import { connect } from 'react-redux'

function ThongTinTroChoi(props) {
    return (
        <div className='row '>
            <h1>BN CHN: <span style={{ color: 'red' }}>{props.xucXac[0].luaChon}</span></h1>
            <h1>S bn thng: <span style={{ color: 'green' }}>{props.xucXac[0].banThang}</span></h1>
            <h1>Tng s bn chi: <span style={{ color: 'blue' }}>{props.xucXac[0].banChoi}</span></h1>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        xucXac: state.stateXucXac.xucXac
    }
}


export default connect(mapStateToProps)(ThongTinTroChoi);